



























export default {
	data() {
		return {
			videoCtx: null,
			disable: false
		};
	},
	mounted() {
		this.videoCtx = uni.createVideoContext('demoVideo');
	},
	methods: {
		play(event) {
			this.videoCtx.play();
			uni.showToast({
				title: '开始播放',
				icon: 'none'
			});
		},
		pause(event) {
			this.videoCtx.pause();
			uni.showToast({
				title: '暂停播放',
				icon: 'none'
			});
		}
	}
};
